// src/components/WellnessChart/WellnessChart.js

import React from 'react';
import { 
    LineChart, 
    Line, 
    XAxis, 
    YAxis, 
    CartesianGrid, 
    Tooltip, 
    Legend, 
    ResponsiveContainer 
} from 'recharts';
import './WellnessChart.css'; // –õ–æ–∫–∞–ª—å–Ω—ñ —Å—Ç–∏–ª—ñ

// –ö–æ–º–ø–æ–Ω–µ–Ω—Ç –≥—Ä–∞—Ñ—ñ–∫–∞ Recharts
const WellnessChart = ({ data, dataKey, title, lineColor = 'var(--primary-color)' }) => {
    
    // –§–æ—Ä–º–∞—Ç—É—î–º–æ –¥–∞–Ω—ñ: Recharts –≤–∏–º–∞–≥–∞—î –º–∞—Å–∏–≤ –æ–±'—î–∫—Ç—ñ–≤
    const formattedData = data.map(record => ({
        // –§–æ—Ä–º–∞—Ç—É—î–º–æ –¥–∞—Ç—É –¥–ª—è –æ—Å—ñ X (–Ω–∞–ø—Ä–∏–∫–ª–∞–¥, "12/03")
        date: record.timestamp ? new Date(record.timestamp).toLocaleDateString('uk-UA', { day: '2-digit', month: '2-digit' }) : '–ù/–î',
        // –ó–±–µ—Ä—ñ–≥–∞—î–º–æ —Ñ–∞–∫—Ç–∏—á–Ω–µ –∑–Ω–∞—á–µ–Ω–Ω—è
        value: record[dataKey],
    })).filter(item => item.value !== undefined); // –í–∏–¥–∞–ª—è—î–º–æ –∑–∞–ø–∏—Å–∏ –±–µ–∑ –∑–Ω–∞—á–µ–Ω–Ω—è

    if (formattedData.length === 0) {
        return <div className="no-chart-data">–ù–µ–¥–æ—Å—Ç–∞—Ç–Ω—å–æ –¥–∞–Ω–∏—Ö –¥–ª—è –≤—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è –≥—Ä–∞—Ñ—ñ–∫–∞ {title}.</div>;
    }

    // 

    return (
        <div className="chart-container-wrapper">
            <h3>{title}</h3>
            <ResponsiveContainer width="100%" height={350}>
                <LineChart
                    data={formattedData}
                    margin={{ top: 10, right: 30, left: 0, bottom: 5 }}
                >
                    <CartesianGrid strokeDasharray="3 3" stroke="var(--chart-grid-color)" />
                    
                    {/* –í—ñ—Å—å X: –î–∞—Ç–∏ */}
                    <XAxis dataKey="date" stroke="var(--text-muted)" />
                    
                    {/* –í—ñ—Å—å Y: –ü–æ–∫–∞–∑–Ω–∏–∫–∏ 1-10 */}
                    <YAxis 
                        domain={[1, 10]} 
                        stroke="var(--text-muted)" 
                        allowDecimals={false}
                        tickCount={10} 
                    />
                    
                    {/* –ü—ñ–¥–∫–∞–∑–∫–∏ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–Ω—ñ */}
                    <Tooltip 
                        contentStyle={{ backgroundColor: 'var(--card-bg)', border: 'none' }}
                        itemStyle={{ color: lineColor }}
                    />
                    
                    {/* –õ–µ–≥–µ–Ω–¥–∞ (–Ω–∞–∑–≤–∞ –ª—ñ–Ω—ñ—ó) */}
                    <Legend />
                    
                    {/* –°–∞–º–∞ –ª—ñ–Ω—ñ—è –≥—Ä–∞—Ñ—ñ–∫–∞ */}
                    <Line 
                        type="monotone" 
                        dataKey="value" 
                        name={title}
                        stroke={lineColor}
                        strokeWidth={2}
                        dot={{ r: 4 }}
                        activeDot={{ r: 6 }}
                    />
                </LineChart>
            </ResponsiveContainer>
        </div>
    );
};

export default WellnessChart;