// src/hooks/useUserData.js
import { useState, useEffect } from 'react';
import { doc, onSnapshot } from 'firebase/firestore';
import { useAuthState } from 'react-firebase-hooks/auth';
import { auth, db } from '../firebase'; 

const useUserData = () => {
    const [user, loadingAuth] = useAuthState(auth);
    const [userData, setUserData] = useState(null);
    
    // –í—Å—Ç–∞–Ω–æ–≤–ª—é—î–º–æ loadingData –≤ true, –ø–æ–∫–∏ –Ω–µ –æ—Ç—Ä–∏–º–∞—î–º–æ –ø–µ—Ä—à–∏–π —Ä–µ–∑—É–ª—å—Ç–∞—Ç –∑ useAuthState
    const [loadingData, setLoadingData] = useState(true); 

    useEffect(() => {
        // 1. –ß–µ–∫–∞—î–º–æ, –ø–æ–∫–∏ useAuthState –≤–∏–∑–Ω–∞—á–∏—Ç—å—Å—è
        if (loadingAuth) return; 
        
        // –í—Å—Ç–∞–Ω–æ–≤–ª—é—î–º–æ loadingData –≤ true –ø—Ä–∏ –∑–º—ñ–Ω—ñ –∫–æ—Ä–∏—Å—Ç—É–≤–∞—á–∞, —è–∫—â–æ –≤—ñ–Ω —î.
        // –¶–µ —Å–ø—Ä–∞—Ü—é—î –ª–∏—à–µ –æ–¥–∏–Ω —Ä–∞–∑ –ø—ñ—Å–ª—è –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è useAuthState.
        if (user) {
            setLoadingData(true); 
            const docRef = doc(db, 'users', user.uid);
            
            // onSnapshot –≤—Å—Ç–∞–Ω–æ–≤–ª—é—î –ø–æ—Å—Ç—ñ–π–Ω–µ –ø—Ä–æ—Å–ª—É—Ö–æ–≤—É–≤–∞–Ω–Ω—è –¥–∞–Ω–∏—Ö –∫–æ—Ä–∏—Å—Ç—É–≤–∞—á–∞
            const unsubscribe = onSnapshot(docRef, 
                (docSnap) => {
                    if (docSnap.exists()) {
                        setUserData({ ...docSnap.data(), uid: user.uid }); // ‚≠ê –ü–û–ö–†–ê–©–ï–ù–ù–Ø: –î–æ–¥–∞—î–º–æ UID –¥–æ –¥–∞–Ω–∏—Ö
                    } else {
                        // –Ø–∫—â–æ –¥–æ–∫—É–º–µ–Ω—Ç –Ω–µ –∑–Ω–∞–π–¥–µ–Ω–æ (—Ö–æ—á–∞ –≤—ñ–Ω —î —É –≤–∞—à—ñ–π –±–∞–∑—ñ), –ø–æ–≤–µ—Ä—Ç–∞—î–º–æ –ª–∏—à–µ UID —Ç–∞ email
                        setUserData({ uid: user.uid, email: user.email }); 
                    }
                    // –í–ê–ñ–õ–ò–í–û: setLoadingData(false) –º–∞—î —Å–ø—Ä–∞—Ü—é–≤–∞—Ç–∏ –ø—ñ—Å–ª—è –ø–µ—Ä—à–æ—ó –≤—ñ–¥–ø–æ–≤—ñ–¥—ñ
                    setLoadingData(false); 
                }, 
                (error) => {
                    console.error("–ü–æ–º–∏–ª–∫–∞ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è –¥–∞–Ω–∏—Ö –∫–æ—Ä–∏—Å—Ç—É–≤–∞—á–∞:", error);
                    // –ù–∞–≤—ñ—Ç—å —É —Ä–∞–∑—ñ –ø–æ–º–∏–ª–∫–∏ –º–∏ –ø—Ä–∏–ø–∏–Ω—è—î–º–æ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è
                    setLoadingData(false);
                }
            );

            // –§—É–Ω–∫—Ü—ñ—è –æ—á–∏—â–µ–Ω–Ω—è (–≤—ñ–¥–ø–∏—Å–∫–∞ –≤—ñ–¥ —Å–ª—É—Ö–∞—á–∞)
            return () => unsubscribe(); 

        } else {
            // –ö–æ—Ä–∏—Å—Ç—É–≤–∞—á –≤–∏–π—à–æ–≤ –∞–±–æ –Ω–µ —É–≤—ñ–π—à–æ–≤
            setUserData(null);
            setLoadingData(false);
        }
    }, [user, loadingAuth]); 

    return { userData, loading: loadingData };
};

export default useUserData;