// src/components/DashboardCard/DashboardCard.js

import React from 'react';
import './DashboardCard.css'; // –Ü–º–ø–æ—Ä—Ç—É—î–º–æ —Å—Ç–∏–ª—ñ

// title, value, unit, description, icon - —Ü–µ –¥–∞–Ω—ñ –∑ lastRecord
// color - –≤–∏–∫–æ—Ä–∏—Å—Ç–æ–≤—É—î—Ç—å—Å—è –¥–ª—è –ø—ñ–¥—Å–≤—ñ—á—É–≤–∞–Ω–Ω—è –∫–∞—Ä—Ç–∫–∏ (–Ω–∞–ø—Ä–∏–∫–ª–∞–¥, success, danger, primary)
const DashboardCard = ({ title, value, unit, description, icon, color = 'var(--primary-color)' }) => {
    return (
        // –í—Å—Ç–∞–Ω–æ–≤–ª—é—î–º–æ border-left-color –≤—ñ–¥–ø–æ–≤—ñ–¥–Ω–æ –¥–æ –ø–µ—Ä–µ–¥–∞–Ω–æ–≥–æ –∫–æ–ª—å–æ—Ä—É
        <div className="dashboard-card" style={{ borderLeftColor: color }}>
            <div className="card-header">
                {/* –Ü–∫–æ–Ω–∫–∞ –∑ –∫–æ–ª—å–æ—Ä–æ–º */}
                <span className="card-icon" style={{ color: color }}>
                    {icon} 
                </span>
                <h3 className="card-title">{title}</h3>
            </div>
            
            <div className="card-body">
                <div className="card-value-group">
                    <span className="card-value">{value}</span>
                    {unit && <span className="card-unit">{unit}</span>}
                </div>
                <p className="card-description">{description}</p>
            </div>
        </div>
    );
};

export default DashboardCard;