// src/hooks/useLastWellnessRecord.js
import { useState, useEffect } from 'react';
import { collection, query, orderBy, limit, onSnapshot } from 'firebase/firestore';
import { useAuthState } from 'react-firebase-hooks/auth';
import { auth, db } from '../firebase'; 

// –•—É–∫ –ø–æ–≤–µ—Ä—Ç–∞—î –æ—Å—Ç–∞–Ω–Ω—ñ–π –∑–∞–ø–∏—Å Wellness
const useLastWellnessRecord = () => {
    const [user] = useAuthState(auth);
    const [lastRecord, setLastRecord] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        if (!user) {
            setLastRecord(null);
            setLoading(false);
            return;
        }

        setLoading(true);
        setError(null);

        const recordsRef = collection(db, 'users', user.uid, 'wellnessRecords');
        
        // –ó–∞–ø–∏—Ç: —Å–æ—Ä—Ç—É—î–º–æ –∑–∞ —Å–ø–∞–¥–∞–Ω–Ω—è–º (–Ω–∞–π–Ω–æ–≤—ñ—à–∏–π –∑–≤–µ—Ä—Ö—É) —ñ –æ–±–º–µ–∂—É—î–º–æ –¥–æ –æ–¥–Ω–æ–≥–æ
        const q = query(
            recordsRef,
            orderBy('timestamp', 'desc'), 
            limit(1) 
        );

        const unsubscribe = onSnapshot(q, 
            (snapshot) => {
                if (snapshot.docs.length > 0) {
                    const doc = snapshot.docs[0];
                    setLastRecord({
                        id: doc.id,
                        ...doc.data()
                    });
                } else {
                    setLastRecord(null);
                }
                setLoading(false);
            }, 
            (err) => {
                console.error("–ü–æ–º–∏–ª–∫–∞ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è –æ—Å—Ç–∞–Ω–Ω—å–æ–≥–æ –∑–∞–ø–∏—Å—É Wellness:", err);
                setError(err.message);
                setLoading(false);
            }
        );

        return () => unsubscribe(); 
        
    }, [user]); 

    return { lastRecord, loading, error };
};

export default useLastWellnessRecord;