import { useState, useEffect } from 'react';
import { collection, query, orderBy, where, onSnapshot } from 'firebase/firestore'; 
import { useAuthState } from 'react-firebase-hooks/auth';
import { auth, db } from '../firebase'; 

// –•—É–∫ –ø–æ–≤–µ—Ä—Ç–∞—î –≤—Å—ñ –∑–∞–ø–∏—Å–∏ Wellness –∑–∞ –æ—Å—Ç–∞–Ω–Ω—ñ N –¥–Ω—ñ–≤
const useWellnessRecordsHistory = (days = 30) => {
    const [user] = useAuthState(auth);
    const [history, setHistory] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        // –Ø–∫—â–æ –∫–æ—Ä–∏—Å—Ç—É–≤–∞—á –Ω–µ –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω–∏–π, –Ω—ñ—á–æ–≥–æ –Ω–µ –∑–∞–≤–∞–Ω—Ç–∞–∂—É—î–º–æ
        if (!user) {
            setHistory([]);
            setLoading(false);
            return;
        }

        setLoading(true);
        setError(null);

        // 1. –û–±—á–∏—Å–ª—é—î–º–æ –¥–∞—Ç—É –ø–æ—á–∞—Ç–∫—É (—Å—å–æ–≥–æ–¥–Ω—ñ –º—ñ–Ω—É—Å –∫—ñ–ª—å–∫—ñ—Å—Ç—å –¥–Ω—ñ–≤)
        const startDate = new Date();
        startDate.setDate(startDate.getDate() - days);

        // 2. –ü–æ—Å–∏–ª–∞–Ω–Ω—è –Ω–∞ –ó–ê–ì–ê–õ–¨–ù–£ –∫–æ–ª–µ–∫—Ü—ñ—é 'wellnessRecords'
        const recordsRef = collection(db, 'wellnessRecords');
        
        // 3. –°—Ç–≤–æ—Ä—é—î–º–æ —Å–∫–ª–∞–¥–Ω–∏–π –∑–∞–ø–∏—Ç
        // –í–ê–ñ–õ–ò–í–û: –¶–µ–π –∑–∞–ø–∏—Ç –ø–æ—Ç—Ä–µ–±—É—î –Ü–ù–î–ï–ö–°–£ —É Firebase (–ø–æ—Å–∏–ª–∞–Ω–Ω—è –±—É–¥–µ –≤ –∫–æ–Ω—Å–æ–ª—ñ –±—Ä–∞—É–∑–µ—Ä–∞)
        const q = query(
            recordsRef,
            where('userId', '==', user.uid), // –¢—ñ–ª—å–∫–∏ –¥–∞–Ω—ñ –ø–æ—Ç–æ—á–Ω–æ–≥–æ –∫–æ—Ä–∏—Å—Ç—É–≤–∞—á–∞
            where('timestamp', '>=', startDate), // –ó–∞ –≤–∫–∞–∑–∞–Ω–∏–π –ø–µ—Ä—ñ–æ–¥
            orderBy('timestamp', 'asc') // –í—ñ–¥ —Å—Ç–∞—Ä—ñ—à–∏—Ö –¥–æ –Ω–æ–≤—ñ—à–∏—Ö –¥–ª—è –≥—Ä–∞—Ñ—ñ–∫–∞
        );

        // 4. –°–ª—É—Ö–∞—á —Ä–µ–∞–ª—å–Ω–æ–≥–æ —á–∞—Å—É
        const unsubscribe = onSnapshot(q, 
            (snapshot) => {
                const recordsList = snapshot.docs.map(doc => {
                    const data = doc.data();
                    return {
                        id: doc.id,
                        ...data, // –°–ø–æ—á–∞—Ç–∫—É –∫–æ–ø—ñ—é—î–º–æ –≤—Å—ñ –ø–æ–ª—è
                        // –ü–æ—Ç—ñ–º –ü–ï–†–ï–ó–ê–ü–ò–°–£–Ñ–ú–û timestamp, –∫–æ–Ω–≤–µ—Ä—Ç—É—é—á–∏ –π–æ–≥–æ –∑ Firebase Format —É JS Date
                        timestamp: data.timestamp?.toDate() 
                    };
                });
                setHistory(recordsList); 
                setLoading(false);
            }, 
            (err) => {
                console.error("–ü–æ–º–∏–ª–∫–∞ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è —ñ—Å—Ç–æ—Ä—ñ—ó Wellness:", err);
                setError(err.message);
                setLoading(false);
            }
        );

        // –í—ñ–¥–ø–∏—Å—É—î–º–æ—Å—å –≤—ñ–¥ —Å–ª—É—Ö–∞—á–∞ –ø—Ä–∏ –≤–∏–¥–∞–ª–µ–Ω–Ω—ñ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
        return () => unsubscribe(); 

    }, [user, days]); 

    return { history, loading, error };
};

export default useWellnessRecordsHistory;